<template>
    <label :class="wrapClasses">
            <span :class="radioClasses">
                <span :class="innerClasses"></span>
                <span :class="correctClasses"></span>
                <input
                    v-if="group"
                    type="checkbox"
                    :class="inputClasses"
                    :disabled="disabled"
                    :value="label"
                    :name="groupName"
                    @change="change"
                    v-model="model"
                   >
                <input
                v-else
                type="checkbox"
                :class="inputClasses"
                :disabled="disabled"
                :checked="currentValue"
                :name="groupName"
                @change="change"
                >
            </span>
        <slot> <span>{{ label }}</span></slot>
        <i class="move-active"></i>
    </label>
</template>
<script>
import { findComponentUpward } from './../utils/assist';
const prefixCls = 'gt-checkbox'
export default {
    name: 'Checkbox',
    data(){
        return {
            currentValue: this.value,
            parents: findComponentUpward(this, 'CheckboxGroup'),
            group: false,
            model: [],
            changeActive: false
        }
    },
    props:{
        label: {
            type: String,
            default: ''
        },
        groupName:{
            type: [String, Number],
            default: ''
        },
        disabled: {
            type: Boolean,
            default: false
        },
        value:{
            type: [String, Number, Boolean],
            default: false
        }
    },
    computed:{
        wrapClasses() {
            return [
                `${prefixCls}-wrapper`,
                {
                    [`${prefixCls}-group-item`]: this.group,
                    [`${prefixCls}-wrapper-checked`]: this.currentValue,
                    [`${prefixCls}-wrapper-disabled`]: this.disabled,
                    [`${prefixCls}-change`]: this.changeActive,
                },
            ];
        },
        radioClasses() {
            return [
                `${prefixCls}`,
                {
                    [`${prefixCls}-checked`]: this.currentValue,
                    [`${prefixCls}-disabled`]: this.disabled,
                },
            ];
        },
        innerClasses() {
            return [
                `${prefixCls}-inner`,
            ];
        },
        correctClasses() {
            return `${prefixCls}-correct`;
        },
        inputClasses() {
            return `${prefixCls}-input`;
        }
    },
    methods:{
        change(event) {
            if (this.disabled) { return false; }
            const checked = event.target.checked;
            this.currentValue = checked;
            this.$emit('input',this.currentValue);
            if (this.group) {
                this.parents.change(this.model);
            } else {
                this.$emit('on-change',this.currentValue);
            }
            if (checked) {
                this.changeActive = true;
            } else {
                this.changeActive = false;
            }
        },
        updateValue() {
            this.currentValue = this.value;
        }
    },
    mounted(){
        if (this.parents) {
            this.group = true;
        }
        if (this.group) {
            this.parents.updateValue();
        } else {
            this.updateValue();
        }
    }

    
}
</script>

<style lang="scss">
@import '../style/color';
@import '../style/scap';
.gt-checkbox{
    display: inline-block;
    white-space: nowrap;
    position: relative;
    line-height: 1;
    vertical-align: middle;
    cursor: pointer;
     margin-right: $select-padding-left;
    &-wrapper{
        vertical-align: middle;
        display: inline-block;
        position: relative;
        white-space: nowrap;
        cursor: pointer;
        margin-right: $select-padding-left;
    }
    &-change .move-active::after{
        animation: active-data .4s ease-in
        // transform: scale(1.5);
        // opacity: .5
    }
    &-inner{
        display: inline-block;
        width: $radio-width-base;
        height: $radio-width-base;
        position: relative;
        background-color: white;
        border-radius: 4px;
        border: 1px solid $input-border-base;
        transition: all .2s ease-in-out;
        &::after{
            position: absolute;
            width: $radio-width-base;
            height: $radio-width-base;
            border-radius: 4px;
            display: table;
            border-top: 0;
            border-left: 0;
            content: ' ';
            background-color: #2461F6;
            opacity: 0;
            transition: all 0.2s ease-in-out;
            transform: scale(0);
        }
    }
    &-correct{
        position: absolute;
        background-image: url("data:image/png;base64,");
        background-repeat: no-repeat;
        background-size: 100%;
        transform: translate(-2px,1px);
        opacity: 0;
    }
    &-input{
        position: absolute;
        top:0;
        z-index: 0;
        opacity: 0;
        cursor: pointer;
    }
     &-checked .gt-checkbox-inner{
         border:1px solid #2461F6;
     }
    &-checked .gt-checkbox-inner:after{
        opacity: 1;
        transform: scale(1.1);
        transition: all .2s ease-in-out;
    }

    &-checked .gt-checkbox-correct{
        width: 18px;
        height: 18px;
        display: inline-block;
        z-index: 3;
        background-size: 108%;
        left: 1px;
        transition: all 0.2s 0.1s ease-in-out;
        transform: translate(0px, -1px);
        opacity: 1;
        top:2px;
    }

}
.move-active{
    width: 18px;
    height: 18px;
    position: absolute;
    left: 1px;
    top:2px;
}
.move-active::after{
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background-color: rgba(157,181,248,.8);
    opacity: 0
}
@keyframes active-data {
    0% {
        -webkit-transform: scale(.2);
        transform: scale(.2);
        opacity: 1
    }

    50% {
        -webkit-transform: scale(1.5);
        transform: scale(1.5);
        opacity: .5
    }

    to {
        -webkit-transform: scale(1.7);
        transform: scale(1.7);
        opacity: 0
    }
}

</style>
